import axios from 'axios'
import router from '@/router'
import store from '@/store'
import { Loading, Message } from 'element-ui'

let loading
function startLoading () {
  loading = Loading.service({
    lock: true,
    text: '...',
    background: 'rgba(0, 0, 0, 0.7)'
  })
}
function endLoading () {
  loading.close()
}

/* axios */
axios.defaults.baseURL = 'http://192.168.5.182:8080'

// Token;
if (store.state.app.token) {
  const { type, access } = store.state.app.token
  axios.defaults.headers.common['Authorization'] = `${type} ${access}`
}

// 
axios.interceptors.request.use(config => {
  // httploading
  startLoading()
  return config
}, error => {
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(response => {
  // http 
  endLoading()

  if (response.status === 401) {
    store.commit('CLEAR_HISTORY_ROUTES')
    store.dispatch('CLEAR_TOKEN')

    axios.defaults.headers.common['Authorization'] = ''
    router.push({ name: 'LoginUsername' })
    Message.warning('Token')
  }
  return response
}, error => {
  endLoading() // 

  // token

  if (error.response && error.response.status === 401) {
    store.dispatch('CLEAR_TOKEN')
    axios.defaults.headers.common['Authorization'] = ''
    router.push({ name: 'LoginUsername' })
    Message.warning('Token')
    return
  }

  switch (error.status) {
    case '401':
      store.dispatch('CLEAR_TOKEN')
      axios.defaults.headers.common['Authorization'] = ''
      router.push({ name: 'LoginUsername' })
      Message.warning('Token')
      break
    case '403':
      console.log('HTTPToken')
      break
    case '404':
      console.log('HTTP')
      break
    case '500':
      console.log(``)
      break
    default:
      console.log(``)
  }
  return Promise.reject(error)
})

export default axios
