<template lang="html">
  <div>
    <el-dialog :visible.sync="dialogVisible" title="" width="760px">
      <el-form
        :model="formData"
        :rules="rules"
        label-width="120px"
        size="small"
        class="clearfix"
        ref="formUpdate"
      >
        <el-form-item prop="name" label="">
          <el-input v-model.trim="formData.name" placeholder="" />
        </el-form-item>

        <FormSelect
          @on-change="$set(formData, 'gameConfigId', $event)"
          :value="formData.gameConfigId"
          httpAPIName="fetchGameClasses"
          labelAttr="name"
          valueAttr="id"
          prop="gameConfigId"
          label=""
          ref="gameConfigId"
        />

        <el-form-item prop="upperLimit" label="">
          <el-input v-model.trim="formData.upperLimit" type="number" min="0" placeholder="" />
        </el-form-item>

        <el-form-item prop="lowerLimit" label="">
          <el-input v-model.trim="formData.lowerLimit" type="number" min="0" placeholder="" />
        </el-form-item>

        <el-form-item prop="ratio" label="%">
          <el-input v-model.trim="formData.ratio" type="number" min="0" placeholder="" />
        </el-form-item>

        <el-form-item prop="status" label="">
          <el-radio-group v-model="formData.status">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>

        <FormTimeSelect
          @on-change="$set(formData, 'startTime', $event)"
          :time="formData.startTime"
          prop="startTime"
          label=""
          placeholder=""
          ref="startTime"
        />

        <FormTimeSelect
          @on-change="$set(formData, 'endTime', $event)"
          :time="formData.endTime"
          prop="endTime"
          label=""
          placeholder=""
          ref="endTime"
        />

        <FormSelect
          @on-change="$set(formData, 'rebateUserGroups', $event)"
          :value="formData.rebateUserGroups"
          httpAPIName="fetchUserGroups"
          :httpAPIParams="{ params: { pageNo: 1, pageSize: 100 } }"
          labelAttr="name"
          valueAttr="groupId"
          prop="rebateUserGroups"
          label=""
          multiple
          ref="rebateUserGroups"
        />
      </el-form>

      <span slot="footer">
        <el-button @click="submitForm('formUpdate')" type="primary" size="small"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { dialogUpdateMixin } from '@/mixins'

import FormSelect from '@/components/form/FormSelect'
import FormRadio from '@/components/form/FormRadio'
import FormTimeSelect from '@/components/form/FormTimeSelect'

export default {
  name: 'RebateSettingDialogCreate',
  components: {
    FormSelect,
    FormRadio,
    FormTimeSelect
  },
  mixins: [ dialogUpdateMixin ],
  data () {
    return {
      updateHttpAPI: 'createRebateSettingList',
      idParams: {}, // Id/
      formData: { name: '', gameConfigId: '', upperLimit: '', lowerLimit: '', ratio: '', rebateUserGroups: [], status: 0 },
      rules: {
        name: { required: true, message: '' },
        gameConfigId: { required: true, message: '' },
        upperLimit: [
          { required: true, message: '' },
          { pattern: /^[0-9]+$/, message: '' }
        ],
        lowerLimit: [
          { required: true, message: '' },
          { pattern: /^[0-9]+$/, message: '' }
        ],
        ratio: { required: true, message: '' },
        status: { required: true, message: '' },
        rebateUserGroups: { required: true, message: '', trigger: 'change' }
      }
    }
  },
  watch: {
    data () {
      this.idParams = { id: this.data.id }
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item {
  float: left;
  width: 50%;
}
</style>
