<template lang="html">
  <div>
    <el-table :data="data" size="small" highlight-current-row border>
      <el-table-column type="index" :min-width="30" />

      <el-table-column prop="name" label="" :min-width="100" />

      <el-table-column prop="interfaceTypeDesc" label="" />

      <el-table-column prop="payTypeName" label="" />

      <el-table-column prop="merchantId" label="" />

      <el-table-column prop="discountRatio" label="" :width="70">
        <template slot-scope="scope">
          <span>{{ scope.row.discountRatio | percent }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="payAddress" label="" />

      <el-table-column prop="sysGroupNames" label="" />

      <el-table-column prop="status" label="" :width="45">
        <template slot-scope="scope">
          <BaseIndicator :status="scope.row.status" opposite />
        </template>
      </el-table-column>

      <!-- <el-table-column prop="limitStatus" label="" :width="45">
        <template slot-scope="scope">
          <BaseIndicator :status="scope.row.limitStatus" opposite />
        </template>
      </el-table-column>

      <el-table-column prop="fineTuningStatus" label="" :width="45">
        <template slot-scope="scope">
          <BaseIndicator :status="scope.row.fineTuningStatus" opposite />
        </template>
      </el-table-column>

      <el-table-column prop="pcStatus" label=" " :width="60">
        <template slot-scope="scope">
          <BaseIndicator :status="scope.row.pcStatus" opposite />
        </template>
      </el-table-column>

      <el-table-column prop="phoneStatus" label=" " :width="60">
        <template slot-scope="scope">
          <BaseIndicator :status="scope.row.pcStatus" opposite />
        </template>
      </el-table-column> -->

      <el-table-column prop="sort" label="" :width="45" />

      <el-table-column prop="remark" label="" :min-width="120" />

      <el-table-column prop="operations" label="">
        <template slot-scope="scope">
          <el-button @click="showDialogDelete(scope.row)" type="primary" icon="el-icon-delete" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <DialogDeleteConfirm
      @on-confirm="handleDeleteConfirm"
      title="()"
      :name="activeItem.name"
      ref="dialogDelete"
    />
  </div>
</template>

<script>
import BaseIndicator from '@/components/base/BaseIndicator'
import DialogDeleteConfirm from '@/components/dialog/DialogDeleteConfirm'

export default {
  name: 'PaymentLineTable',
  components: {
    BaseIndicator,
    DialogDeleteConfirm
  },
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      activeItem: { name: '' }
    }
  },
  methods: {
    showDialogDelete (item) {
      this.activeItem = item
      this.$refs.dialogDelete.toggleDialogStatus(true)
    },
    handleDeleteConfirm () {
      this.$refs.dialogDelete.toggleDialogStatus(false)

      this.$httpAPI.deleteTransactionPortLine({
        payTypeId: this.activeItem.payTypeId,
        payInterfaceIds: [ this.activeItem.id ]
      }).then(response => {
        if (response.data.status === 200) {
          this.$emit('on-deleted')
          this.$message.success('')
        } else {
          this.$message.error('')
        }
      }).catch(error => console.log(error))
    }
  }
}
</script>
