<template lang="html">
  <div class="remittance-create">
    <el-dialog :visible.sync="dialogVisible" title="" width="760px">
      <el-form
        :model="formData"
        :rules="rules"
        label-width="100px"
        size="small"
        class="clearfix"
        ref="formCreate"
      >
        <el-form-item prop="username" label="">
          <el-input v-model.trim="formData.username" placeholder="" />
        </el-form-item>

        <el-form-item label="">
          <el-radio-group v-model="receiptWay">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>

        <template v-if="!receiptWay">
          <el-form-item label="" class="custom-block">
            <el-upload
              action="http://192.168.5.182:8080/api-f/files-anon/fdfsupload"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-success="handleUploadSuccess"
              :disabled="uploadDisabled"
              >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogImageVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
          </el-form-item>
        </template>
        <template v-else>
          <el-form-item prop="bank" label="">
            <el-input v-model.trim="formData.bank" placeholder="" />
          </el-form-item>

          <el-form-item prop="userAccount" label="" class="custom-block">
            <el-input v-model.trim="formData.userAccount" placeholder="" />
          </el-form-item>

          <el-form-item prop="bankAddress" label="" class="custom-block">
            <el-input v-model.trim="formData.bankAddress" placeholder="" />
          </el-form-item>
        </template>

        <el-form-item prop="sort" label="">
          <el-input v-model.trim="formData.sort" type="number" min="0" max="99" placeholder="" />
        </el-form-item>

        <el-form-item prop="discountRatio" label="%">
          <el-input v-model.trim="formData.discountRatio" type="number" placeholder="" />
        </el-form-item>

        <FormSelect
          @on-change="$set(formData, 'userGroups', $event)"
          :value="formData.userGroups"
          httpAPIName="fetchUserGroups"
          :httpAPIParams="{ params: { pageNo: 1, pageSize: 100 } }"
          labelAttr="name"
          valueAttr="groupId"
          prop="userGroups"
          label=""
          multiple
        />
      </el-form>

      <span slot="footer">
        <el-button @click="submitForm('formCreate')" type="primary" size="small"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormSelect from '@/components/form/FormSelect'

export default {
  name: 'UserRemittanceDialogCreate',
  components: {
    FormSelect
  },
  data () {
    return {
      dialogImageUrl: '',
      dialogImageVisible: false,
      uploadDisabled: false,
      receiptWay: 0,
      dialogVisible: false,
      formData: { sort: 0, userGroups: [] },
      rules: {
        username: { required: true, message: '' },
        discountRatio: { required: true, message: '' },
        // codeUrl: { required: true, message: '' },
        sort: [
          { required: true, message: '' },
          { pattern: /^[0-9]{1,2}$/, message: ' 0 - 99 ' }
        ],
        // bank: { required: true, message: '' },
        // userAccount: { required: true, message: '' },
        // bankAddress: { required: true, message: '' }
        userGroups: { required: true, message: '' }
      }
    }
  },
  methods: {
    handleUploadSuccess (response) {
      this.formData.codeUrl = response
      this.uploadDisabled = true
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
      this.uploadDisabled = true
    },
    toggleDialogVisible (status) {
      this.dialogVisible = status
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogVisible = false

          this.$httpAPI.createTransactionRemittance(this.formData).then(response => {
            if (response.data.status === 200) {
              this.$emit('on-created')
              this.$message.success('')
            }
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item {
  float: left;
  width: 50%;
}
</style>
