<template lang="html">
  <div class="remittance-update">
    <el-dialog :visible.sync="dialogVisible" title="" width="760px">
      <el-form
        :model="formData"
        :rules="rules"
        label-width="100px"
        size="small"
        class="clearfix"
        ref="formCreate"
      >
        <el-form-item prop="username" label="">
          <el-input v-model.trim="formData.username" placeholder="" />
        </el-form-item>

        <el-form-item prop="discountRatio" label="%">
          <el-input v-model.trim="formData.discountRatio" type="number" placeholder="" />
        </el-form-item>

        <el-form-item prop="sort" label="">
          <el-input v-model.trim="formData.sort" type="number" placeholder="" />
        </el-form-item>

        <el-form-item prop="codeUrl" label="" class="custom-block">
          <el-input v-model.trim="formData.codeUrl" placeholder="" />
        </el-form-item>

        <el-form-item prop="bank" label="" class="custom-block" >
          <el-input v-model.trim="formData.bank" placeholder=""/>
        </el-form-item>

        <el-form-item prop="userAccount" label="" class="custom-block">
          <el-input v-model.trim="formData.userAccount" placeholder="" />
        </el-form-item>

        <el-form-item prop="bankAddress" label="" class="custom-block">
          <el-input v-model.trim="formData.bankAddress" placeholder="" />
        </el-form-item>

        <FormSelect
          @on-change="$set(formData, 'userGroups', $event)"
          :value="formData.userGroups"
          httpAPIName="fetchUserGroups"
          :httpAPIParams="{ params: { pageNo: 1, pageSize: 100 } }"
          labelAttr="name"
          valueAttr="groupId"
          prop="userGroups"
          label=""
          multiple
        />
      </el-form>

      <span slot="footer">
        <el-button @click="submitForm('formCreate')" type="primary" size="small"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormSelect from '@/components/form/FormSelect'

export default {
  name: 'UserRemittanceDialogUpdate',
  components: {
    FormSelect
  },
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false,
      formData: {},
      rules: {
        username: { required: true, message: '' },
        // bank: { required: true, message: '' },
        discountRatio: { required: true, message: '' },
        // codeUrl: { required: true, message: '' },
        sort: { required: true, message: '' },
        userGroups: { type: 'array', required: true, message: '', trigger: 'change' }
        // userAccount: { required: true, message: '' },
        // bankAddress: { required: true, message: '' }
      }
    }
  },
  watch: {
    data () {
      this.formData = this.data
    }
  },
  methods: {
    toggleDialogVisible (status) {
      this.dialogVisible = status
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogVisible = false

          this.$httpAPI.updateTransactionRemittance(this.formData).then(response => {
            if (response.data.status === 200) {
              this.$emit('on-updated')
              this.$message.success('')
            } else {
              this.$message.error('')
            }
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item {
  float: left;
  width: 50%;
}
</style>
