<template lang="html">
  <div class="manipulation-form">
    <!--  -->
    <el-form :model="formData" :rules="rules" ref="balanceForm" label-width="120px">
      <el-form-item prop="username" label="">
        <el-input v-model="formData.username" placeholder="" />
      </el-form-item>

      <FormAccountType @on-change="formData.gameType = $event" ref="formAccountType" />

      <el-form-item prop="type" label="">
        <el-radio-group v-model="formData.type">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
        </el-radio-group>
      </el-form-item>

      <FormLoanType @on-change="formData.loanType = $event" ref="formLoanType" />

      <el-form-item prop="money" label="">
        <el-input v-model="formData.money" type="number" min="0" placeholder="" />
      </el-form-item>

      <el-form-item prop="remark" label="">
        <el-input v-model="formData.remark" @keyup.native.enter="submitForm('balanceForm')" type="textarea" :rows="3" placeholder="" />
      </el-form-item>

      <el-form-item>
        <el-button @click="submitForm('balanceForm')" type="primary" size="medium"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import FormLoanType from '@/components/form/FormLoanType'
import FormAccountType from '@/components/form/FormAccountType'

export default {
  name: 'BalanceManipulationForm',
  components: {
    FormLoanType,
    FormAccountType
  },
  data () {
    var validateUsername = (rule, value, callback) => {
      this.$httpAPI.fetchUserId({ params: { username: value } }).then(response => {
        if (response.data.data === null) {
          callback(new Error(''))
        } else {
          callback()
        }
      }).catch(error => console.log(error))
    }

    return {
      loanTypes: [],
      gameTypes: [],
      formData: {
        username: '', gameType: '', type: '', loanType: '', money: '', remark: ''
      },
      rules: {
        username: [
          { required: true, message: '' },
          { validator: validateUsername, trigger: 'blur' }
        ],
        gameType: { required: true, message: '' },
        type: { required: true, message: '' },
        loanType: { required: true, message: '' },
        money: [
          { required: true, message: '' }
          // { required: true, message: '0' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.saveFinanceBalanceChange(this.formData).then(() => {
            // 
            this.$utils.initializeObjectProperties(this.formData)
            this.$refs.formLoanType.reset()
            this.$refs.formAccountType.reset()

            this.$emit('on-created')
            this.$message.success('')
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    }
  }
}
</script>
